const uni = require('uni').default;
































































export default {
	data() {
		return {}
	},
	components: {},
	methods: {
		baocun() {
			uni.getImageInfo({
				src: 'http://duxinggj.com/uploads/upload_9572a98f1f9e1aeecc896f507cd27b26.jpg',
				success: function (sres) {
					uni.saveImageToPhotosAlbum({
						filePath: sres.path,
						success(res) {
							uni.showToast({
								title: '保存成功',
								icon: 'none',
								duration: 2000
							})
						}
					})
				}
			})
		}
	},
	mounted() {},
}



